:root {
  --black: hsl(225deg 9.52% 8.24%);
  --dark-grey: hsl(228deg 7.94% 12.35%);
  --grey: hsl(223.64deg 12.94% 16.67%);
  --light-grey: hsl(228deg 12.2% 40.2%);
  --white: #f7f7f7;
  --green: hsl(139.4117647059deg 58.6206896552% 54.5098039216%);
  --danger: hsl(354deg 70.5% 53.5%);
  --font-family: 'Montserrat', 'DejaVu Sans', 'Verdana', sans-serif;
  --border-gradient: #ff2400, #e81d1d, #e8b71d, #e3e81d, #1de840, #1ddde8, #2b1de8, #dd00f3, #ff2400;
  --divider-gradient: #ff2400, #e81d1d, #e8b71d, #e3e81d, #1de840, #1ddde8, #2b1de8, #dd00f3;
}
.root-layout {
  min-height: 100vh;
  background-color: hsl(225deg 9.52% 8.24%);
  background-color: var(--black);
  position: relative;
  font-family: 'Montserrat', 'DejaVu Sans', 'Verdana', sans-serif;
  font-family: var(--font-family);
  color: #f7f7f7;
  color: var(--white);
  display: flex;
  flex-direction: column;
}
* {
  margin: 0;
}

.hero-wrapper {
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: 1rem 1.5rem;
  font-size: 2vw;
}
.hero-wrapper > h2 {
  text-align: center;
  margin-bottom: 2.5rem;
  background-image: linear-gradient(45deg, #5cff0e, #0056ff);
  background-size: 100%;
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
}
.hero-wrapper code {
  font-family: monospace;
  background-color: var(--light-grey);
  border-radius: 5px;
  padding: 0.2rem;
}
.hero {
  display: flex;
  border-radius: 30px;
  padding: 2rem 1rem 0;
}
.bttm-right-grad {
  background-image: linear-gradient(to bottom right, var(--dark-grey) 10%, var(--black));
}
.bttm-left-grad {
  background-image: linear-gradient(to bottom left, var(--dark-grey) 10%, var(--black));
}
.hero-story {
  text-align: center;
  flex: 1 1;
}
.hero-story * {
  margin-bottom: 1.5rem;
}

.hero-story p {
  font-size: 1.4vw;
  line-height: 1.8;
}
.hero-card {
  flex: 1 1;
}
.hero-card-1 {
  padding: 0 0 1rem 1rem;
}
.hero-card-2 {
  padding: 0 1rem 1rem 0;
}
.hero-showcase img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.action-btn {
  border-radius: 4px;
  border: none;
  color: #ffffff;
  font-size: 1rem;
  padding: 0.5rem 2rem;
  cursor: pointer;
  background: var(--green);
  text-decoration: none;
}

img {
  -webkit-animation: opac 1s;
          animation: opac 1s;
}
@-webkit-keyframes opac {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes opac {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@media screen and (max-width: 800px) {
  .hero {
    flex-direction: column;
  }
  .action-btn {
    font-size: 0.8rem;
    padding: 0.4rem 1.8rem;
  }
}

.action-bar-wrapper {
  background-color: var(--dark-grey);
  display: flex;
  justify-content: flex-end;

  border-radius: 5px 5px 0 0;
  position: relative;
}
.action-bar-wrapper button:nth-child(4) {
  border-radius: 0 5px 0 0;
}
.action-bar-wrapper button {
  background: var(--dark-grey);
  color: #ffffff;
  border: none;
  padding: 7px 14px;
  cursor: pointer;
}
.action-bar-wrapper .fas:hover {
  -webkit-transform: scale(1.3, 1.3);
          transform: scale(1.3, 1.3);
  transition: 0.3s;
}

.mark {
  position: absolute;
  left: 15px;
  top: 8px;
  color: #ffffff;
}

.action-bar-divider {
  height: 1px;
  background: linear-gradient(124deg, var(--divider-gradient));
  -webkit-animation: rainbow 15s ease infinite;
          animation: rainbow 15s ease infinite;
  background-size: 200%;
}

@-webkit-keyframes rainbow {
  0% {
    background-position: 0% 82%;
  }
  50% {
    background-position: 100% 19%;
  }
  100% {
    background-position: 0% 82%;
  }
}

@keyframes rainbow {
  0% {
    background-position: 0% 82%;
  }
  50% {
    background-position: 100% 19%;
  }
  100% {
    background-position: 0% 82%;
  }
}

.code-editor {
  height: 100%;
  width: 100%;
}
.code-wrapper {
  height: 100%;
  width: calc(100% - 10px);
  position: relative;
}
.format-btn {
  position: absolute;
  bottom: 5px;
  right: 16px;
  z-index: 5;
  border-radius: 8px;
  border: none;
  font-family: Arial;
  color: #ffffff;
  font-size: 12px;
  padding: 7px 10px;
  cursor: pointer;
  background: var(--green);
  text-decoration: none;
  opacity: 0;
  transition: opacity 0.3s;
}
.code-wrapper:hover .format-btn {
  opacity: 1;
}

/* JSX-highlighter */
.mtk100.Identifier.JsxElement.Bracket {
  color: rgb(45, 112, 228);
}

.mtk1000.Identifier.JsxOpeningElement.Bracket {
  color: rgb(45, 112, 228);
  font-weight: bold;
}

.mtk1001.Identifier.JsxClosingElement.Bracket {
  color: rgb(45, 112, 228);
  font-weight: lighter;
}

.mtk101.Identifier.JsxOpeningElement.Identifier {
  color: steelblue;
}

.mtk102.Identifier.JsxClosingElement.Identifier {
  color: steelblue;
  font-weight: lighter;
}

.mtk103.Identifier.JsxAttribute.Identifier {
  color: steelblue;
}

.mtk104.JsxElement.JsxText {
  color: rgb(255, 255, 255);
}

.mtk105.glyph.Identifier.JsxElement {
  background: #61dafb;
  opacity: 0.25;
}

.iframe-wrapper {
  flex: 1 100;
  position: relative;
  height: 100%;
  overflow: hidden;
}
/* Fix issue with drag event transfering to iframe */
.react-draggable-transparent-selection .iframe-wrapper::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.iframe-wrapper iframe {
  height: 100%;
  width: 100%;
  border-style: none;
  background-color: white;
}
.bundler-error {
  font-family: Consolas, Courier New, monospace;
  color: red;
  position: absolute;
  top: 8px;
  left: 8px;
}

.resizable-container-v {
  width: 100%;
}
.resizable-container-h {
  display: flex;
}

.react-resizable-handle {
  display: block;
  background-color: var(--dark-grey);
  background-repeat: no-repeat;
  background-position: 50%;
}

.react-resizable-handle-s {
  height: 10px;
  width: 100%;
  border-radius: 0 0 5px 5px;
  cursor: row-resize;
  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAB4AAAAFAQMAAABo7865AAAABlBMVEVHcEzMzMzyAv2sAAAAAXRSTlMAQObYZgAAABBJREFUeF5jOAMEEAIEEFwAn3kMwcB6I2AAAAAASUVORK5CYII=');
}

.react-resizable-handle-e {
  width: 10px;
  min-width: 10px;
  height: 100%;
  cursor: col-resize;
  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAeCAYAAADkftS9AAAAIklEQVQoU2M4c+bMfxAGAgYYmwGrIIiDjrELjpo5aiZeMwF+yNnOs5KSvgAAAABJRU5ErkJggg==');
}

.codecell {
  display: flex;
  height: calc(100% - 10px);
  width: 100%;
}
.spinner-wrapper {
  height: 100%;
  flex: 100 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
}
@-webkit-keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes spinner {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.loading-spinner {
  width: 20px;
  height: 20px;
  border: 10px solid var(--grey);
  border-top: 10px solid var(--green);
  border-radius: 50%;
  -webkit-animation: spinner 1.5s linear infinite, fadeIn 0.7s;
          animation: spinner 1.5s linear infinite, fadeIn 0.7s;
}

.editor-wrapper {
  background-color: transparent;
}
.md-wrapper {
  padding: 1.5rem;
  border-radius: 0 0 5px 5px;
  background-color: var(--dark-grey);
  box-shadow: 1px 15px 20px #000000b3;
}
.md-wrapper .wmde-markdown {
  background-color: var(--dark-grey);
}
.w-md-editor-bar svg {
  display: none;
}
.w-md-editor-bar {
  background-color: var(--dark-grey);
  background-repeat: no-repeat;
  background-position: 50%;
  height: 10px;
  width: 100%;
  cursor: row-resize;
  border-radius: 0 0 3px 3px;
  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAB4AAAAFAQMAAABo7865AAAABlBMVEVHcEzMzMzyAv2sAAAAAXRSTlMAQObYZgAAABBJREFUeF5jOAMEEAIEEFwAn3kMwcB6I2AAAAAASUVORK5CYII=');
}
.w-md-editor-toolbar {
  background-color: var(--grey);
}
.w-md-editor {
  box-shadow: none;
}
.w-md-editor-content,
.wmde-markdown {
  background-color: #202124;
}

.add-cell-wrapper {
  width: 95%;
  position: relative;
  background-color: transparent;
  display: flex;
  justify-content: center;
  margin: 6px auto;
  opacity: 0;
  transition: opacity 0.3s ease-in 0.1s;
}
.add-cell-wrapper:hover {
  opacity: 1;
}
.add-cell-wrapper .divider {
  position: absolute;
  height: 1px;
  width: 100%;
  background-color: var(--grey);
  top: 50%;
  z-index: 1;
}
.add-cell-wrapper button {
  height: 40px;
  width: 40px;
  background: var(--light-grey);
  color: var(--white);
  border: none;
  cursor: pointer;
  border-radius: 20px;
  margin: 0 20px;
  z-index: 2;
}
.add-cell-wrapper button:hover {
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
  transition: 0.3s;
}
.add-cell-wrapper.force-visible {
  -webkit-animation: forceVis 0.8s;
          animation: forceVis 0.8s;
  opacity: 1;
}
@-webkit-keyframes forceVis {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes forceVis {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.title-wrapper {
  display: flex;
  flex-direction: column;
  border-radius: 4px;
  grid-gap: 1rem;
  gap: 1rem;
  background-color: var(--grey);
  margin-bottom: 1.5rem;
  padding: 2rem;
}

.cell-list-wrapper {
  padding: 2rem 10vw;
}

.thread-item-wrapper {
  padding: 1.5rem;
  background-color: var(--grey);
  color: var(--white);
  height: 150px;
  width: 200px;
  overflow-y: auto;
  position: relative;
}
.thread-item-wrapper:hover {
  cursor: pointer;
  --angle: 0deg;
  border: 1px solid;
  border-image: conic-gradient(from var(--angle), var(--border-gradient)) 1;
  -webkit-animation: rotate 10s linear infinite;
          animation: rotate 10s linear infinite;
}
.thread-item-wrapper h3 {
  overflow-wrap: break-word;
  margin-bottom: 2rem;
}
.thread-item-wrapper p {
  overflow-wrap: break-word;
}

.thread-item-wrapper::-webkit-scrollbar {
  width: 5px;
}
.thread-item-wrapper::-webkit-scrollbar-thumb {
  width: 10px;
  height: 3px;
  background-color: var(--white);
  border-radius: 100px;
}
.thread-item-wrapper::-webkit-scrollbar-track {
  background-color: var(--light-grey);
}
.action-buttons {
  display: flex;
  position: absolute;
  top: 0;
  right: 5px;
  background: transparent;
}
.action-buttons button {
  background: transparent;
  color: var(--white);
  border: none;
  cursor: pointer;
}
.action-buttons button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: 0.1s;
}
@property --angle {
  syntax: '<angle>';
  initial-value: 0deg;
  inherits: false;
}
@-webkit-keyframes rotate {
  to {
    --angle: 360deg;
  }
}
@keyframes rotate {
  to {
    --angle: 360deg;
  }
}

.add-thread-item-wrapper {
  background-color: var(--grey);
  color: var(--white);
  height: 198px;
  width: 248px;
  overflow: hidden;
}
.add-thread-item-wrapper:hover {
  cursor: pointer;
}
.add-thread-item-wrapper a {
  text-decoration: none;
  color: var(--white);
}

.icon-wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.icon-wrapper:hover {
  -webkit-transform: scale(1.3, 1.3);
          transform: scale(1.3, 1.3);
  transition: 0.3s;
}

.thread-list-wrapper {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  grid-column-gap: 1.5rem;
  -webkit-column-gap: 1.5rem;
          column-gap: 1.5rem;
  grid-row-gap: 1.5rem;
  row-gap: 1.5rem;
  align-content: flex-start;
  margin: 2rem 1rem;
  min-height: 100vh;
  overflow-y: hidden;
}
.thread-list-wrapper a {
  text-decoration: none;
}

.add-thread-wrapper {
  min-height: 100vh;
}
.add-thread-wrapper form {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  padding: 3rem;
}
.add-thread-wrapper form button {
  border-radius: 4px;
  border: none;
  color: #ffffff;
  font-size: 0.9rem;
  padding: 0.5rem 0.75rem;
  cursor: pointer;
  background: var(--green);
  text-decoration: none;
}
.add-thread-wrapper form button[name='discard'] {
  background: var(--danger);
}
.button-wrapper {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  justify-content: flex-end;
}
.button-wrapper button {
  flex: 1 1;
}

