.footer {
  background-color: black;
  color: white;
  padding: 1rem 0;
  margin-top: auto;
}
.footer-content {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  align-items: center;
  font-size: 0.8rem;
}
.footer-content a:hover {
  transform: scale(1.2, 1.2);
  transition: 0.3s;
}
